.dialogs {
	min-height: calc(100vh - 60px);
	.dialogItem {
		border-bottom: 1px solid #000;
        width: 100%;
        max-width: 100%;
		a {
			display: block;
			color: #000;
			padding: 10px 20px 10px 10px;
			background: rgb(150, 136, 231);
			box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.8);
            width: 100%;
            max-width: 100%;
			.dialogLink {
				display: flex;
                width: 100%;
                max-width: 100%;
                margin: 0 auto;
				.dialogContent {
					margin: 15px;
					width: 100%;
					max-width: 75%;
					.dialogName {
						font-weight: 700;
						font-size: 17px;
						text-shadow: 0.5px 1px 0.5px rgba(0, 0, 0, 0.2);
					}
					.message {
						margin: 5px 0 0 5px;
                        width: 100%;
                        max-width: 100%;
						background: rgb(226, 234, 253);
						line-height: 15px;
						padding: 4px;
						max-height: 35px;
						overflow: hidden;
						border-radius: 5px;
						box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
						background: rgb(174, 164, 231);
					}
				}
			}
		}
		.dialogPhoto img {
			margin-top: 5px;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			box-shadow: 3px 3px 12px rgba(0, 0, 0, 1);
		}
	}
}
